@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*Gamas de colores de paletton:
Violetas: #400340 #571157-#864086 #9D619D #6F256F
Azulado: #226764 #033C3A #10514F #3B7D7B #5B9391
Tierra: F2D495
*/
.App {
  text-align: center;
  font-family: 'Josefin Sans', sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.art-dev-container{
    /* background: rgb(87,17,87);
    background: linear-gradient(193deg, rgba(87,17,87,1) 0%, rgba(16,81,79,1) 100%); */
    background: rgb(103,61,148);
    background: linear-gradient(29deg, rgba(103,61,148,1) 0%, rgba(247,172,130,1) 100%);
    width: 100vw;
    height: 100vh;
}
.art-dev-avatar{
    width: 500px;
}
.button {
    background: none;
    border-radius: 10px;
    padding: 10px;
    color: #fff;
    font-size: 20px;
    cursor: pointer;
    border: 1px solid #fff;
    margin: 10px;
}
.main-container{
    /* background-color: #a186be; */
    background: rgb(103,61,148);
    background: linear-gradient(29deg, rgba(103,61,148,1) 0%, rgba(247,172,130,1) 100%);
    /* background: rgb(87,17,87);
    background: linear-gradient(193deg, rgba(87,17,87,1) 0%, rgba(16,81,79,1) 100%); */
    display: flex;
    width: 100vw;
    height: 100vh;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    color:#fff;
}
.avatar-description-container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.avatar-image{
    width: 500px;   
}
.avatar-container{
    flex-grow: 1;
}
.personal-description{
    flex-grow: 2;
}
.personal-description h1{
    font-size: 50px;
}
.personal-description span{
    font-size: 30px;
}
@media(max-width:520px){
    .avatar-image{
        width: 90%;
    }
    .personal-description h1{
        font-size: 40px;
    }
    .personal-description span{
        font-size: 25px;
    }
}
.navbar-container{
    display: flex;
    justify-content: flex-end;
}
.navbar-container{
    margin: 0;
    padding: 0;
}
.navbar-container li{
    margin: 0 10px;
    list-style: none;
    font-size: 20px;
    cursor: pointer;
}
.navbar-container li a {
    text-decoration: none;
    color: #fff;
}
